@import '../untils/variables';

.recording {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto 1fr;

  &__lid{
    grid-column: 1/2;
    grid-row: 1/2;
    display: flex;
    justify-content: center;
  }

  &__date {
    grid-column: 2/3;
    grid-row: 1/2;
  }

  &__pre {
    grid-column: 3/4;
    grid-row: 1/2;
    display: flex;
    justify-content: center;
  }

  &__table {
    grid-column: 1/4;
    grid-row: 2/3;
  }

  &__text {
    padding: 10px 15px;
    font-size: 17px;
    font-weight: 700;
    line-height: 18px;
  }
}
